import React from 'react';
import Service from './Service';
import design from '../../assets/Services/designer.json';
import add from '../../assets/Services/add animation.json';
import integration from '../../assets/Services/integration.json'
import seo from '../../assets/Services/seo.json'
import maintenance from '../../assets/Services/maintenance.json'
import training from '../../assets/Services/remote-training.json'

const Services = () => {
    

   const services = [
        {
            id: 1,
            serviceName: 'Design and Development of Websites and Mobile Applications Systems',
            animation: design,
        },
        {
            id: 2,
            serviceName: 'Integration of Websites with Databases and other back-end systems',
            animation: add,
        },
        {
            id: 3,
            serviceName: 'Creation of Responsive user-friendly and virtually appealing user interfaces',
            animation: integration,
        },
        {
            id: 4,
            serviceName: 'Optimization of website contents for search engines(SEO) and performance',
            animation: seo,
        },
        {
            id: 5,
            serviceName: 'Implementation of security measures to protect website and user data',
            animation: training,
        },
        {
            id: 6,
            serviceName: 'Maintenance and update to keep website and applications running smoothly',
            animation: maintenance,
        },
    ];
    return (
        <div>
            <h1 className="text-lg lg:text-3x1 font-bold text-secondary text-center py-10">My Services</h1>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-10 md:grid-cols-1 py-5">
                {services.map((service, i) => <Service key={i} service={service}></Service>)
                
                }
            </div>
        </div>
    );
};

export default Services;