import React from 'react';
import './ContactMe.css'

const ContactMe = () => {
    return (
        <div>
            <div className="bg-base-200 p-10">
            <div className="grid grid-cols-1 lg:grid-cols-2 lg:gap-5 gap-3">
                <div className="text-center w-[300px] lg:w-full">
                <h1 className="text-3xl font-bold">Welcome to my contact page!</h1>
                <p className="text-left py-6">If you have any inquiries or want to collaborate on a project, 
                please don't hesitate to reach out to me. You can contact me via at <a href="segunharuna@gmail.com" className='text-secondary'>segunharuna@gmail.com</a> or 
                through the form provided below. Please provide a 
                detailed message along with your name and email address. I will do my best to 
                respond to your message within 24 hours. Thank you for considering me for 
                your web development needs. I look forward to working with you!</p>
                </div>
                <div className="w-[300px] lg:w-full flex flex-col  items-center">
                    
                    <form className="contact-form">
                        <input
                            type="email"
                            placeholder="Enter your email"
                            className="email-input"
                        />
                        <input type="text" placeholder="Enter your Phone Number" className="number-input">
                        
                        </input>
                        <textarea
                            placeholder="Enter your message"
                            className="message-input"
                        />
                        <button type="submit" className="submit-button">
                            Submit
                        </button>
                        </form>
                </div>
            </div>
            </div>
        </div>
    );
};

export default ContactMe;