import React from 'react';
import Lottie from 'lottie-react'
import webDevAnimation from '../../assets/Banner/64110-web-development.json'
import Typewriter from 'typewriter-effect';

const Banner = () => {

    return (
        <div>
            <div className="bg-base-200 p-10">
                <div className="grid grid-cols-1 lg:grid-cols-2 lg:gap-5 gap-3">
                    <div className='w-[300px] lg:w-full'>
                    <Lottie animationData={webDevAnimation} loop={true} />
                    </div>
                    
                    <div className='w-[300px] lg:w-full flex flex-col justify-center items-center'>
                        <h1 className="lg:text-3xl text-secondary font-bold">
                        <Typewriter
                                options={{
                                    strings: ['Hi, my name is Segun Haruna', 'A Frontend Web Developer'],
                                    autoStart: true,
                                    loop: true,
                                }}
                            />
                        </h1>
                        <p className="py-6 lg:text-lg text-sm text-center">
                            I am a frontend web developer. 
                            I have a passion for creating visually appealing and user-friendly 
                            websites and enjoy bringing ideas to life through code. I have a strong 
                            understanding of HTML, CSS, and JavaScript and am constantly learning and 
                            improving my skills. I am a detail-oriented individual who is dedicated to 
                            producing high-quality work and meeting project deadlines. In my free time, 
                            I enjoy staying up-to-date with the latest web development trends and 
                            technologies. I am excited to use my skills to help businesses and organizations 
                            achieve their online goals.
                        </p>
                       
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Banner;