import React from 'react';
  import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import MyPhoto from '../../assets/AboutMe/1672263456987.jpg'


const AboutMe = () => {
    const [openTab, setOpenTab] = React.useState(1);
    return (
        <div>
            
             <div className="bg-base-200 p-10">
             <h1 className="text-lg lg:text-3x1 font-bold text-secondary text-center py-10">My Journey</h1>
                <div className="grid grid-cols-1 lg:grid-cols-2 lg:gap-5 gap-3">
                    <div className='w-[300px] lg:w-full'>
                        <img src={MyPhoto} alt="" />
                    </div>
                    
                    <div className='w-[300px] lg:w-full flex flex-col  items-center'>

                        <div className="w-full">
                            <ul
                                className="flex mb-0 list-none flex-wrap pt-3 pb-4 flex-row"
                                role="tablist">
                                <li className="-mb-px mr-2 last:mr-0 flex-auto text-center">
                                    <a
                                        className={
                                        "text-xs font-bold uppercase px-5 py-3 shadow-lg rounded block leading-normal " +
                                        (openTab === 1
                                            ? "text-secondary bg-600"
                                            : "text-600 bg-base-200")
                                        }
                                        onClick={e => {
                                        e.preventDefault();
                                        setOpenTab(1);
                                        }}
                                        data-toggle="tab"
                                        href="About"
                                        role="tablist">
                                        <i className="fas fa-space-shuttle text-base mr-1 "></i> About Me
                                    </a>
                                </li>
                                <li className="-mb-px mr-2 last:mr-0 flex-auto text-center">
                                    <a
                                        className={
                                        "text-xs font-bold uppercase px-5 py-3 shadow-lg rounded block leading-normal " +
                                        (openTab === 2
                                            ? "text-secondary bg-600"
                                            : "text-600 bg-base-200")
                                        }
                                        onClick={e => {
                                        e.preventDefault();
                                        setOpenTab(2);
                                        }}
                                        data-toggle="tab"
                                        href="Skills"
                                        role="tablist">
                                        <i className="fas fa-cog text-base mr-1"></i>  Skills
                                    </a>
                                </li>
                                <li className="-mb-px mr-2 last:mr-0 flex-auto text-center">
                                    <a
                                        className={
                                        "text-xs font-bold uppercase px-5 py-3 shadow-lg rounded block leading-normal " +
                                        (openTab === 3
                                            ? "text-secondary bg-600"
                                            : "text-600 bg-base-200")
                                        }
                                        onClick={e => {
                                        e.preventDefault();
                                        setOpenTab(3);
                                        }}
                                        data-toggle="tab"
                                        href="Others"
                                        role="tablist">
                                        <i className="fas fa-briefcase text-base mr-1"></i>  Other Job Experience
                                    </a>
                                </li>
                            </ul>
                            <div className="relative flex flex-col min-w-0 break-words bg-base-200 w-full mb-6 shadow-lg rounded">
                                <div className="px-4 py-5 flex-auto">
                                <div className="tab-content tab-space">
                                    <div className={openTab === 1 ? "block" : "hidden"} id="link1">
                                    <p>
                                        As a skilled web developer with over 3 years experience, 
                                        in my current role at Veep Technology, i am responsible for designing, developing, 
                                        and maintaining websites and web applications. I work with a 
                                        team of developers and designers to create user-friendly and visually 
                                        appealing websites using a variety of coding languages, such as HTML, 
                                        CSS, and JavaScript. I am responsible for quality assurance by testing and 
                                        debugging websites to ensure that they function properly and meet 
                                        the needs of clients.
                                        <br />
                                        <br /> In addition to my abilities, I am a quick learner, I stay 
                                        up-to-date with the latest trends and technologies in web development.
                                        <br />
                                        <br />
                                        I look forward to the opportunity to discuss your project further and demonstrate 
                                        my abilities
                                    </p>
                                    </div>
                                    <div className={openTab === 2 ? "block" : "hidden"} id="link2">
                               
                                        <li><FontAwesomeIcon icon="fab fa-html5" />HTML</li>
                                        <li><FontAwesomeIcon icon="fab fa-css3-alt" />CSS</li>
                                        <li><FontAwesomeIcon icon="fab fa-js-square" />JavaScript</li>
                                        <li><FontAwesomeIcon icon="fab fa-bootstrap" />Bootstrap</li>
                                        <li><FontAwesomeIcon icon="fab fa-react" />React</li>
                                        <li><FontAwesomeIcon icon="fas fa-database" />Database</li>
                                        <li><FontAwesomeIcon icon="fab fa-node-js" />NodeJS</li>
                                        <li><FontAwesomeIcon icon="fab fa-wordpress" />WordPress</li>
                                        <br />
                                        <br />
                                    </div>
                                    <div className={openTab === 3 ? "block" : "hidden"} id="link3">
                                    <p>
                                        With my 15 years cumulative working experience, I have been
                                        privileged to work in Construction, Telecommunication, ICT, Aviation
                                        Industry.
                                        <br />
                                        <br /> I have experience in working in these roles as a:
                                        <br />
                                        <br />
                                        <li>Customer Care Agent</li>
                                        <li>Credit Control Officer</li>
                                        <li>Billing Officer</li>
                                        <li>IT and Application Support (ERP)</li>
                                        <li>IT Project Manager</li>
                                        <li>Data Analyst </li>
                                    </p>
                                    </div>
                                </div>
                                </div>
                            </div>
                        </div>
                        
                    </div>   
                </div>
                
            </div>
            
        </div>
    );
};

export default AboutMe;